import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Fiscal_sif } from '../../../modelo/Fiscal_sif'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'fiscal_sif-dados-details',   templateUrl: 'fiscal_sif_dadosDetails.component.html' }) export class Fiscal_sifDadosDetailsComponent implements OnInit {    fiscal_sif: Fiscal_sif;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.fiscal_sif.Fiscal_sif";                 let id = +params['id'];         this.fiscal_sif = new Fiscal_sif();         this.fiscal_sif.cd_fiscal_sif = id;         o.objetoJson = this.fiscal_sif;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a fiscal_sif. '; }      this.fiscal_sif = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


